import {ParameterGroupServicesProvider} from '../../providers/parameter-group-services/parameter-group-services';
import {GlobalComponent} from '../../components/global/global';
import {LoadingController} from 'ionic-angular';
import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { ParameterGroupViewModel } from "../../Model/ViewModel/ParameterGroupViewModel";

/**
 * Generated class for the ParameterGroupModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-parameter-group-add-edit-modal",
  templateUrl: "parameter-group-add-edit-modal.html"
})
export class ParameterGroupAddEditModalPage {
  item: any;
  mode: string;
  CanEditJob: boolean;
  title:string;
  ParameterGroup_type:string;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    private ParameterGroupServices:ParameterGroupServicesProvider,
    public loadingCtrl: LoadingController,
    private global: GlobalComponent,
  ) {

    this.item = new ParameterGroupViewModel();
    this.item.parameter_column_group=navParams.data.item.parameter_column_group;
    this.item.parameter_group_desc=navParams.data.item.parameter_group_desc;
    this.item.creator=navParams.data.item.creator;
    this.item.create_time=navParams.data.item.create_time;
    this.item.modifier=navParams.data.item.modifier;
    this.item.last_update_time=navParams.data.item.last_update_time;


    this.CanEditJob = navParams.data.CanEditJob;
    this.mode = navParams.data.mode;
    this.title="參數群組";
  }
  get_max_parameter_column_group() {
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.ParameterGroupServices.GetMaxParameterGroupNoAsync
        (this.item.parameter_column_group)
        .subscribe(
          data => {

            if (data.DidError === true)
            {
              this.global.dismissLoading();
             this.global.showError(data.ErrorMessage);
            }
            else {
              this.item.parameter_column_group=data.Model;
              this.global.dismissLoading();
            }
          },
          err => {
            this.global.dismissLoading();
            this.global.showError("無法連上WebAPI伺服器-"+err.message);
          }
        );

    });
  }

  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad ParameterGroupModalPage");
  }
}



