import {StringResponse} from '../../Model/Response/StringResponse';
import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { ParameterGroupListResponse } from "../../Model/Response/ParameterGroupListResponse";
import { ParameterGroupResponse } from "../../Model/Response/ParameterGroupResponse";
import { ParameterGroupViewModel } from '../../Model/ViewModel/ParameterGroupViewModel';

/*
  Generated class for the ParameterGroupServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class ParameterGroupServicesProvider {
  private ParameterGroups: ParameterGroupListResponse;
  private ParameterGroup: ParameterGroupResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "ParameterGroup";
    console.log("Hello ParameterGroupServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "ParameterGroup?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.ParameterGroups = new ParameterGroupListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.ParameterGroups;
    });
  }



  public PostPutAsync(ParameterGroupViewModel:ParameterGroupViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "ParameterGroup";
    else
    sub_url = "ParameterGroup/{1}";
    sub_url = String.Format(sub_url, this.ctl,ParameterGroupViewModel.parameter_column_group);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,ParameterGroupViewModel,mode).map(
      item => {
        this.ParameterGroup = new ParameterGroupResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.ParameterGroup;
      }
    );
  }

  public DeleteAsync(parameter_column_group:string) {
    var sub_url: string;
    sub_url = "ParameterGroup/{1}";
    sub_url = String.Format(sub_url, this.ctl,parameter_column_group);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.ParameterGroup = new ParameterGroupResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.ParameterGroup;
      }
    );
  }
  public GetMaxParameterGroupNoAsync(parameter_column_group:string) {
    var sub_url: string;
    sub_url = "ParameterGroup/get_max_parameter_group/{1}";
    sub_url = String.Format(sub_url, this.ctl,parameter_column_group);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }


  public GetSqlAsync(parameter_column_group:string,method:string) {
    var sub_url: string;
    sub_url = "ParameterGroup/get_{2}_sql/{1}";
    sub_url = String.Format(sub_url, this.ctl,parameter_column_group,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
}



